import {Input, Directive, ElementRef, NgModule} from '@angular/core';
import {ListKeyManagerOption} from '@angular/cdk/a11y';

@Directive({
  selector: '[adaptFocusMonitor]'
})
export class AdaptFocusMonitorDirective implements ListKeyManagerOption, ElementRef<HTMLOptionElement> {
  /** Option Id */
  @Input('adaptFocusMonitor') optionId?: string | number;


  get disabled(): boolean {
    return this._elementRef.nativeElement.disabled;
  }

  constructor(private _elementRef: ElementRef<HTMLOptionElement>) {
  }

  // TODO: maybe remove this implementation
  getLabel(): string {
    return this._elementRef.nativeElement.innerText;
  }

  get nativeElement(): HTMLOptionElement {
    return this._elementRef.nativeElement;
  }
}

@NgModule({
  declarations: [AdaptFocusMonitorDirective],
  exports: [AdaptFocusMonitorDirective],
})
export class AdaptFocusMonitorDirectiveModule { }
